import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, MenuController, AlertController, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Network } from '@ionic-native/network';
import { MessageService } from '../utils/message-service';
import { LoadingService } from '../utils/loading-service';
import { ObraService } from '../services/obra.service';
import { Storage } from '@ionic/storage';
import { ChecklistService } from '../services/checklist.service';
import { AlteracaoService } from '../services/alteracao.service';
import { Alteracao } from '../models/alteracao';
import { Obra } from '../models/obra';
import { FuncionarioService } from '../services/funcionario.service';
import { StorageServiceUtils } from '../utils/storage-service-utils';
import { FornecedorService } from '../services/fornecedor.service';
import { PermissaoService } from '../services/permissao.service';

@Component({
  templateUrl: 'app.html',
  providers: [ObraService, ChecklistService, AlteracaoService, FuncionarioService, FornecedorService, PermissaoService]
})

export class MyApp {
  rootPage: string = "LoginPage";
  @ViewChild(Nav) nav: Nav;
  pages: any;
  params: any;
  atualizacao: boolean = false;
  statusAtualizacao: string = "";
  idsObraComProblea: number[] = [];

  static progress: number = 0;
  static progressbarAtivo: boolean = false;
  static segundos: number = 0;

  constructor(
    platform: Platform,
    statusBar: StatusBar,
    public menu: MenuController,
    public loadingService: LoadingService,
    public storageServiceUtils: StorageServiceUtils,
    public messageService: MessageService,
    public storage: Storage,
    public obraService: ObraService,
    public alteracaoService: AlteracaoService,
    public funcionarioService: FuncionarioService,
    public fornecedorService: FornecedorService,
    public permissaoService: PermissaoService,
    public alertCtrl: AlertController,
    public checklistService: ChecklistService,
    public network: Network,
    public events: Events,
    splashScreen: SplashScreen) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
      statusBar.backgroundColorByHexString('rgb(33,177,75)');
      this.definirRoot();
      this.definirEventoMenu();
    });

    this.pages = [
      { "title": "Home", "icon": "home", "component": "HomePage", "color": "green" },
      { "title": "Baixar dados", "icon": "cloud-download", "component": "Baixar", "color": "green" },
      { "title": "Publicar alteraes", "icon": "cloud-upload", "component": "Subir", "color": "green" },
      { "title": "Listar alteraes", "icon": "list", "component": "AlteracoesPage", "color": "orange" },
      { "title": "Descartar alteraes", "icon": "trash", "component": "Descartar", "color": "red" },
      { "title": "Cadastrar Checklist", "icon": "checkmark-circle-outline", "component": "ChecklistPage", "color": "orange" },
      { "title": "Acesso s obras", "icon": "build", "component": "ObraPage", "color": "green" },
      { "title": "Sair", "icon": "exit", "component": "LoginPage", "color": "red" },
    ];

    this.params = {
      "background": "assets/images/background/5.jpg",
      "image": "assets/images/logo/logo.png"
    };
  }

  definirEventoMenu() {
    this.events.subscribe('openPage', (page) => {
      this.openPage(page);
    });
  }

  definirRoot() {
    this.storage.ready().then(() => {
      this.storage.get('Usuario').then(
        usuario => {
          if (usuario) {
            localStorage.setItem('isLogged', 'true');
            localStorage.setItem("Usuario", usuario);
            this.storage.get('BancoSchema').then(
              empresa => {
                localStorage.setItem("BanchoSchema", empresa);
              }
            );
            this.rootPage = "HomePage";
          } else {
            this.rootPage = "LoginPage";
          }
        }
      );
    });
  }

  public openPage(page) {
    if (page.component != "") {

      if (page.component == 'Baixar') {
        if (this.network.type === 'none') {
          this.messageService.exibirMensagem("Voc precisa de uma conexo com internet para obter os dados e poder ento trabalhar offline.");
        } else {
          this.baixarDados();
        }
      } else if (page.component == 'Subir') {
        if (this.network.type === 'none') {
          this.messageService.exibirMensagem("Voc precisa de uma conexo com internet para subir os dados trabalhados offline.");
        } else {
          this.subirDados();
        }
      } else if (page.component == 'Descartar') {
        this.descartarDados();
      } else if (page.component == 'LoginPage') {
        this.storage.ready().then(() => {
          this.storage.get('atualizacoes').then(
            atualizacoes => {
              if (atualizacoes) {
                this.messageService.exibirMensagemConfirmacao("Existem atualizaes que no foram publicadas, ao sair da sua conta elas sero perdidas. Deseja sair mesmo assim?", () => { this.deslogar() });
              } else {
                this.messageService.exibirMensagemConfirmacao("Deseja realmente se deslogar?", () => { this.deslogar() });
              }
            }
          );
        });
      } else {
        this.nav.setRoot(page.component);
      }
    }
  }

  async deslogar() {
    localStorage.clear();
    let usuarioSalvo = await this.storage.get('UsuarioSalvo');
    let senha = await this.storage.get('Senha:'+usuarioSalvo);
    let empresa = await this.storage.get('EmpresaSalva');
    this.storage.clear();
    this.storage.set('UsuarioSalvo', usuarioSalvo);
    this.storage.set('Senha:'+usuarioSalvo, senha);
    this.storage.set('EmpresaSalva', empresa);
    this.nav.setRoot("LoginPage");
  }

  baixarDados() {
    this.storage.ready().then(() => {
      this.storage.get('atualizacoes').then(
        atualizacoes => {
          if (atualizacoes) {
            this.messageService.exibirMensagem("Existem atualizaes que no foram publicadas, publique-as ou descarte-as antes de baixar novos dados.");
          } else {
            this.nav.setRoot("HomePage");
            this.obterChecklistServico();
            this.obterFuncionarios();
            this.obterFornecedores();
            this.atualizacao = false;
            this.obterObras();
            this.obterPermissoesExclusao();
          }
        }
      );
    });
  }

  async subirDados() {
    await this.storage.ready();
    let atualizacoes = await this.storage.get('atualizacoes');
    if (atualizacoes) {
      this.confirmarAtualizarRepositorio(atualizacoes);
    } else {
      this.messageService.exibirMensagem("No h nada para publicar.");
    }
  }

  descartarDados() {
    this.storage.ready().then(() => {
      this.storage.get('atualizacoes').then(
        atualizacoes => {
          if (atualizacoes) {
            this.confirmarDescartarDados();
          } else {
            this.messageService.exibirMensagem("No h nada para descartar.");
          }
        }
      );
    });
  }

  confirmarDescartarDados() {
    let mensagem = "Todas as alteraes feitas desde a ltima publicao sero perdidas. Deseja realmente descart-las?";
    this.messageService.exibirMensagemConfirmacao(mensagem, () => { this.executarDescartarDados() });
  }

  executarDescartarDados() {
    this.storage.remove('atualizacoes');
    this.loadingService.show();
    this.storageServiceUtils.montarObraBackup();
    this.nav.setRoot("HomePage");
    this.loadingService.hide();
  }

  obterObras() {
    this.loadingService.show();
    this.idsObraComProblea = [];
    MyApp.progressbarAtivo = true;
    MyApp.progress = 0;
    MyApp.segundos = 0;
    this.controlarSegundos();
    this.subirProgressAutomatico();
    this.subirProgressObterIds();
    this.obraService.obterIdsTodasAtivas().subscribe(
      idsObra => {
        if (idsObra.length > 0) {
          this.obterObraCompleta(idsObra);
        } else {
          MyApp.progressbarAtivo = false;
          MyApp.progress = 0;
          this.loadingService.hide();
        }
      },
      error => {
        MyApp.progressbarAtivo = false;
        MyApp.progress = 0;
        this.loadingService.hide();
        this.messageService.exibirMensagem("Falha na comunicao com o servidor, contate o suporte.");
      }
    );
  }

  controlarSegundos() {
    let segundos = setInterval(() => {
      if (MyApp.progressbarAtivo) {
        MyApp.segundos += 1;
      } else {
        clearInterval(segundos);
      }
    }, 1100);
  }

  subirProgressAutomatico() {
    let obterIds = setInterval(() => {
      if (MyApp.progress < 50) {
        MyApp.progress += 0.2;
      } else if (MyApp.progress < 70) {
        MyApp.progress += 0.1;
      } else if (MyApp.progress < 99) {
        MyApp.progress += 0.05;
      } else {
        clearInterval(obterIds);
      }
    }, 500);
  }

  subirProgressObterIds() {
    let obterIds = setInterval(() => {
      if (MyApp.progress < 15) {
        MyApp.progress += 1;
      } else {
        clearInterval(obterIds);
      }
    }, 300);
  }

  obterObraCompleta(idsObra: number[]) {
    let obras: Obra[] = [];
    let qtdErros: number = 0;
    this.subirProgressObraCompleta();
    idsObra.forEach(idObra => {
      this.obraService.obterObraCompleta(idObra).subscribe(
        obra => {
          obras.push(obra);
          this.setarValoresObras(obras, idsObra, qtdErros);
        },
        error => {
          qtdErros++;
          this.idsObraComProblea.push(idObra);
          this.setarValoresObras(obras, idsObra, qtdErros);
        }
      );
    });
  }

  subirProgressObraCompleta() {
    if (MyApp.progress == 15) {
      let obterIds = setInterval(() => {
        if (MyApp.progress < 40) {
          MyApp.progress += 1;
        } else {
          clearInterval(obterIds);
        }
      }, 300);
    }
  }

  setarValoresObras(obras: Obra[], idsObra: number[], qtdErros: number) {
    if ((obras.length + qtdErros) >= idsObra.length) {
      this.loadingService.hide();
      this.storageServiceUtils.armazenarObraNoStorage(obras, true);
      this.storage.set('ultimoDownload', new Date());
      this.nav.setRoot("HomePage");
      MyApp.progressbarAtivo = false;
      MyApp.progress = 0;
      if (this.statusAtualizacao == "") {
        if (qtdErros > 0) {
          if (this.atualizacao) {
            this.messageService.exibirMensagem("Atualizaes publicadas com sucesso, porm houve um erro ao buscar as obras, tente novamente com uma internet melhor.");
          } else {
            this.messageService.exibirMensagem("Ocorreu erro durante a busca de algumas obras, tente novamente com usando uma internet melhor. Id das obras: "+this.idsObraComProblea.join(", "));
          }
        } else {
          if (this.atualizacao) {
            this.messageService.exibirMensagem("Atualizaes publicadas com sucesso.");
          } else {
            this.messageService.exibirMensagem("Dados recuperados do servidor com sucesso.");
          }
        }
      } else {
        this.messageService.exibirMensagem("Algumas atualizaes no foram realizadas: " + this.statusAtualizacao);
      }
      this.statusAtualizacao = "";
    } else {
      if (MyApp.progress + (60 / idsObra.length) < 75) {
        MyApp.progress += (60 / idsObra.length);
      }
    }
  }

  obterChecklistServico() {
    this.checklistService.obterTodas().subscribe(
      data => {
        this.storage.set('itensChecklist', data);
        this.storage.set('itensChecklistBackup', data);
      },
      error => {
        this.messageService.exibirMensagem("Falha na comunicao com o servidor ao buscar servios, contate o suporte.");
      }
    );
  }

  obterFuncionarios() {
    this.funcionarioService.obterTodos().subscribe(
      data => {
        this.storage.set('funcionarios', data);
      },
      error => {
        this.messageService.exibirMensagem("Falha na comunicao com o servidor ao buscar funcionrios, contate o suporte.");
      }
    );
  }

  obterFornecedores() {
    this.fornecedorService.obterTodos().subscribe(
      data => {
        this.storage.set('fornecedores', data);
      },
      error => {
        this.messageService.exibirMensagem("Falha na comunicao com o servidor ao buscar fornecedores, contate o suporte.");
      }
    );
  }

  async obterPermissoesExclusao() {
    let usuarioSalvo = await this.storage.get('UsuarioSalvo');
    this.permissaoService.obterPermissoesExclusao(usuarioSalvo).subscribe(
      data => {
        this.storage.set('permissoesExclusao', data);
      },
      error => {
        this.messageService.exibirMensagem("Falha na comunicao com o servidor ao buscar permissoes, contate o suporte.");
      }
    );
  }

  confirmarAtualizarRepositorio(atualizacoes: Alteracao[]) {
    let mensagem = "Deseja atualizar o banco com as alteraes realizadas?";
    this.messageService.exibirMensagemConfirmacao(mensagem, () => { this.atualizarRepositorio(atualizacoes) });
  }

  async atualizarRepositorio(atualizacoes: Alteracao[]) {
    this.loadingService.showSubida();
    this.alteracaoService.publicar(atualizacoes).subscribe(
      data => {
        this.storage.set('ultimoUpload', new Date());
        this.storage.remove('atualizacoes');
        this.nav.setRoot("HomePage");
        this.loadingService.hideSubida();
        this.atualizacao = true;
        this.statusAtualizacao = data;
        if (this.statusAtualizacao == "") {
          this.messageService.exibirMensagem("Atualizaes realizadas com sucesso. Realize um novo download dos dados para atualizar o banco de dados do aparelho.");
        } else {
          this.messageService.exibirMensagem("Algumas atualizaes no foram realizadas: " + this.statusAtualizacao);
          this.statusAtualizacao = "";
        }
        this.storageServiceUtils.montarObraBackup();
      },
      error => {
        this.loadingService.hideSubida();
        this.messageService.exibirMensagem("Falha na comunicao com o servidor, contate o suporte.");
      }
    );
  }

  get isLogged() {
    return localStorage.getItem('isLogged');
  }

  get empresa() {
    return localStorage.getItem('BancoSchema');
  }

  get usuario() {
    return localStorage.getItem('Usuario');
  }

}




